import { useState, useEffect } from "react";
import { db } from "../../../../firebaseConfig"; //   firebase
import {
  collection,
  doc,
  getDocs,
  getDoc,
  updateDoc,
  setDoc,
} from "firebase/firestore";
import { useImmer } from "use-immer";

type StudentOption = {
  id: string;
  name: string;
};
interface TestLink {
  id: string; // id 
  variantId: string; // id 
  used: boolean;
  nameStudent: string;
  testResult: string;
  //  ,    
}

import { useVariantContext } from "../variantContext";

const CreatorNewLinkForStudent = (props: {
  selectedVariant: string;
  onClose: () => void;
  updateTestLinks: (dataLink: TestLink) => void;
}) => {
  const [students, updateStudents] = useImmer<StudentOption[]>([]);
  const [selectedStudentId, setSelectedStudentId] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const { dataVariant } = useVariantContext();

  useEffect(() => {
    const fetchStudents = async () => {
      setLoading(true);
      setError(null);
      try {
        const colRef = collection(db, "Subjects", "Math", "MyStudents");
        const snapshot = await getDocs(colRef);

        //      Firestore
        const list: StudentOption[] = snapshot.docs
          .map((doc) => {
            //   
            const data = doc.data() as { name?: unknown };

            // ,    name     
            const name = typeof data.name === "string" ? data.name.trim() : "";

            //  name ,   {id, name},   null
            return name ? { id: doc.id, name } : null;
          })
          //   null,    
          .filter((item): item is StudentOption => item !== null);

        //   students  useImmer
        updateStudents((draft) => {
          draft.length = 0; //   
          list.forEach((student) => draft.push(student)); //   
        });
      } catch (err) {
        console.error("  :", err);
        setError("    .");
      } finally {
        setLoading(false);
      }
    };

    fetchStudents();
  }, []);

  const addLink = async () => {
    if (!selectedStudentId) {
      alert("   ");
      return;
    }
    console.log(selectedStudentId);

    const linkDocId = `${selectedStudentId}_${props.selectedVariant}`;
    const docRef = doc(db, "Subjects", "Math", "TestLinks", linkDocId);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      alert("       ");
      return;
    }

    try {
      //  testScores   
      const docRefStudent = doc(
        db,
        "Subjects",
        "Math",
        "MyStudents",
        selectedStudentId
      );
      await updateDoc(docRefStudent, {
        [`testScores.${dataVariant.variantSerialNumber}`]: " ",
      });
      console.log(" testScores ");

      //       ResultsTest -   
      const resultTestDocRef = doc(
        collection(docRefStudent, "ResultsTest"),
        props.selectedVariant
      );
      await setDoc(resultTestDocRef, {
        userAnswers: {},
        result: " ",
        pointsForTasks: {},
        variantId: props.selectedVariant,
        variantName: dataVariant.variantSerialNumber || dataVariant.variantName,
      });
      console.log(" ResultsTest /");

      //   ( )  TestLinks
      await setDoc(docRef, {
        studentId: selectedStudentId,
        nameStudent:
          students.find((s) => s.id === selectedStudentId)?.name || "",
        variantId: props.selectedVariant,
        testResult: " ",
        used: false,
        createdAt: new Date(),
      });
      console.log(" ");

      interface TestLink {
        id: string; // id 
        variantId: string; // id 
        used: boolean;
        nameStudent: string;
        testResult: string;
        //  ,    
      }

      //   
      props.updateTestLinks({
        id: linkDocId,
        variantId: props.selectedVariant,
        used: false,
        nameStudent:
          students.find((s) => s.id === selectedStudentId)?.name || "",
        testResult: " ",
      } as TestLink);
      //  
      props.onClose();
    } catch (error) {
      console.error(" :", error);
    }
  };

  return (
    <div>
      {loading && <p> ...</p>}
      {error && <p>{error}</p>}
      {!loading && !error && (
        <select
          value={selectedStudentId}
          onChange={(e) => setSelectedStudentId(e.target.value)}
        >
          <option value=""> </option>
          {students.map((student) => (
            <option key={student.id} value={student.id}>
              {student.name}
            </option>
          ))}
        </select>
      )}
      <button onClick={addLink}></button>
    </div>
  );
};
export default CreatorNewLinkForStudent;
